@media screen and (min-width: 48em) {

#screen {
	position: relative;
	height: 100%;
	margin: 0 auto;
	z-index: 0;
	overflow-y: hidden;
}

.main-header {
	margin: 0 0 5px 0;
	padding: 0 12%;
}

#wrapper {
	padding: 0 0.3em 0.3em 0.3em;
}

.tool-bar {
	width: 100%;
	min-height: 40px;
	bottom: 0px;
}

#wrappernav {
	margin: auto;
	padding: 0 12%;
}

#navbar {
	float: right;
	margin-right: -10px;
}

.related {
	margin-top: 12px;
}

.end {
	padding-top: 80px;
}

}

@media screen and (max-width: 48em) {

.btn-github {
	display: none;
}

#screen {
	margin: 0;
	overflow: auto;
}

.main-header {
	position: fixed;
	top: 0;
	width: 100%;
}

.main-header h1 {
	padding: 2px 0;
	margin: 0;
}

.main-logo {
	position: relative;
	width: 70px;
	height: auto;
	display: block;
	margin: auto;
}

#wrapper {
	padding-bottom: 80px;
}

.tool-bar {
	position: fixed;
}

.tool-bar {
	bottom: 0;
	width: 100%;
}

#navbar {
	height: 1.2em;
}

.modeplan #navbar {
	height: 1.6em;
}

.btn-block-settings {
	right: 0;
}

.btn-block-summary {
	right: 2em;
}

/** TOGGLE RELATED LINKS**/

#toggle-nav {
	float: left;
	margin-top: 10px;
}

.related {
	position: absolute;
	bottom: 36px;
	padding: 10px;
}

.related li {
	display: block;
	margin-bottom: 5px;
}

.related li a {
	display: block;
	width: 100%;
}

.is-closed .related {
	display: none;
}

.slide {
	padding: 0.5em;
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	-ms-hyphens: auto;
	hyphens: auto;
	top: 10vh;
}

.tool-block {
	min-height: 100%;
	left: 0;
	top: 0;
}

.tool-block-summary {
	top: 0!important;
}

}

@-webkit-keyframes horizontal {

0% {
	height: 100%;
	width: 99%;
	opacity: 1;
}

100% {
	width: 0%;
	height: 100%;
	opacity: 0.9;
}

}

@keyframes horizontal {

0% {
	height: 100%;
	width: 99%;
	opacity: 1;
}

100% {
	width: 0%;
